 <template>
  <el-main style="text-align:left; line-height: 1.8em;">
    <h3 style="color:#333">
      <span  @click="goToHome" style="cursor: pointer;color:#409EFF">{{this.projectName}}</span> > 
      <el-button type="primary" style="float: right;" @click="dialogFormVisible = true"></el-button>
    </h3>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="form" :rules="rules"  label-width="80px" class="demo-ruleForm">
        <el-form-item label="" prop="ReceivablesName">
          <el-input v-model="form.ReceivablesName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="number">
          <el-input type="number"  v-model="form.number" style="width:46%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="contractdate">
          <el-col :span="11">
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="form.contractdate" style="width: 100%;"></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="contract">
            <el-select v-model="form.contract" placeholder="" style="width:46%" >
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="Receivableslist">
            <el-select v-model="form.Receivableslist" placeholder="" style="width:46%" >
              <el-option label="1" value="1"></el-option>
              <el-option label="2" value="2"></el-option>
              <el-option label="3" value="3"></el-option>
              <el-option label="4" value="4"></el-option>
              <el-option label="5" value="5"></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="ReceivablesMode">
            <el-select v-model="form.ReceivablesMode" placeholder="" style="width:46%" >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="Receivables" v-if="boxvalue1" >
          <el-col :span="11" style="margin-right:10px">
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="form.daozhangdate" style="width: 100%;"></el-date-picker>
          </el-col>
          <el-input type="number"  v-model="form.Receivables" style="width:46%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="invoice" v-if="boxvalue1">
          <el-col :span="11" style="margin-right:10px" v-if="boxvalue" >
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="form.kaifapiaodate" style="width: 100%;"></el-date-picker>
          </el-col>
          <el-input type="number"  v-model="form.invoice" style="width:25%" v-if="boxvalue" ></el-input>
          <el-select v-model="form.invoicebo" placeholder="" style="width:20%" >
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" filterable prop="SupplierName">
            <el-select v-model="form.SupplierName" filterable placeholder="" style="width:46%" >
                <el-option
                v-for="item in optionsOtherParty"
                :key="item.SupplierName"
                :label="item.SupplierName"
                :value="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="projectClass">
            <el-select v-model="form.projectClass" placeholder="" style="width:46%" >
                <el-option
                v-for="item in optionsprojectClass"
                :key="item.expenditureClass"
                :label="item.expenditureClass"
                :value="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="Remarks">
          <el-input type="textarea" v-model="form.Remarks" ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')"></el-button>
          <el-button @click="resetForm('form')"></el-button>
          <el-button @click="dialogFormVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogAddVisible" >
      <el-form ref="formModify" :model="formModify" :rules="rules"  label-width="80px" class="demo-ruleForm">
        <el-form-item label="" prop="ReceivablesName">
          <el-input v-model="formModify.ReceivablesName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="number">
          <el-input type="number"  v-model="formModify.number" style="width:46%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="contractdate">
          <el-col :span="11">
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="formModify.contractdate" style="width: 100%;"></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="ReceivablesMode">
            <el-select v-model="formModify.ReceivablesMode" placeholder="" style="width:46%" >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="contract">
            <el-select v-model="formModify.contract" placeholder="" style="width:46%" >
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="Receivableslist">
            <el-select v-model="formModify.Receivableslist" placeholder="" style="width:46%" >
              <el-option label="1" value="1"></el-option>
              <el-option label="2" value="2"></el-option>
              <el-option label="3" value="3"></el-option>
              <el-option label="4" value="4"></el-option>
              <el-option label="5" value="5"></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="ReceivablesMode">
            <el-select v-model="formModify.ReceivablesMode" placeholder="" style="width:46%" >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="Receivables" v-if="boxvalue1" >
          <el-col :span="11" style="margin-right:10px">
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="formModify.daozhangdate" style="width: 100%;"></el-date-picker>
          </el-col>
          <el-input type="number"  v-model="formModify.Receivables" style="width:46%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="invoice" v-if="boxvalue1">
          <el-col :span="11" style="margin-right:10px" v-if="boxvalue" >
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="formModify.kaifapiaodate" style="width: 100%;"></el-date-picker>
          </el-col>
          <el-input type="number"  v-model="formModify.invoice" style="width:25%" v-if="boxvalue" ></el-input>
          <el-select v-model="formModify.invoicebo" placeholder="" style="width:20%" >
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="SupplierName">
            <el-select v-model="formModify.SupplierName" filterable placeholder="" style="width:46%" >
                <el-option
                v-for="item in optionsOtherParty"
                :key="item.SupplierName"
                :label="item.SupplierName"
                :value="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="projectClass">
            <el-select v-model="formModify.projectClass" placeholder="" style="width:46%" >
                <el-option
                v-for="item in optionsprojectClass"
                :key="item.expenditureClass"
                :label="item.expenditureClass"
                :value="item.id">
                </el-option>
            </el-select>
        </el-form-item>
        <!-- <el-form-item label="" >
          <el-input type="number"  v-model="formModify.Receivables" style="width:46%" ></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-input type="number"  v-model="formModify.invoice" style="width:46%" ></el-input>
        </el-form-item> -->
        <el-form-item label="" prop="Remarks">
          <el-input type="textarea" v-model="formModify.Remarks" ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitFormModify('formModify')"></el-button>
          <el-button @click="dialogAddVisible = false"></el-button>
          <el-button style="float:right" @click="deletepost"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-table :data="tableData" border :summary-method="jsondata.getSummaries" show-summary height='90%' style="width: 100%">
      <el-table-column prop="SupplierName" label=""  width="300" sortable></el-table-column>
      <el-table-column prop="ReceivablesName" label="" sortable></el-table-column>
      <!-- <el-table-column prop="Receivableslist" label="" sortable></el-table-column> -->
      <el-table-column prop="number" label="" sortable></el-table-column>
      <el-table-column prop="contractdate" label="" sortable></el-table-column>
      <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="Receivablesend" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column>
      <el-table-column prop="weikaifapiao" label="" sortable></el-table-column>
      <el-table-column prop="projectClass" label="" sortable></el-table-column>
      <el-table-column prop="" label="" >
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
          <el-button @click="handle(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-main>
</template>
<script>
// import axios from 'axios'

export default {
  data () {
    return {
      boxvalue: true,
      boxvalue1: false,
      hetongjiner: 0, // 
      yingshou: 0, // 
      zongshouru: 0, // 
      weishou: 0, // 
      biujifapiao: 0, // 
      weikaifapiao: 0, // 
      formbf: [],
      projectName: '',
      Customerlist: [],
      tableData: [],
      dialogAddVisible: false,
      dialogFormVisible: false,
      formModify: {}, // 
      TransactionList: {},
      formTransactionCon: '',
      formTransactionList: [], // 
      formTransaction: { // 
        MoneyClass: '',
        CollectMoney: '',
        AmountMoney: '',
        invoice: '',
        projectId: this.$route.params.id
      },
      form: {
        ReceivablesName: '',
        projectId: this.$route.params.id,
        number: '',
        ReceivablesData: '',
        contract: '',
        Remarks: '',
        invoice: '',
        invoicebo: '',
        kaifapiaodate: '',
        daozhangdate: '',
        Receivables: '',
        OtherParty: '',
        ReceivablesMode: '',
        contractdate: '',
        projectClass: ''
      },
      optionsprojectClass: [],
      optionsOtherParty: [],
      rules: {
        ReceivablesName: [
          { required: true, message: '', trigger: 'blur' }
        ]
        // ,
        // ReceivablesData: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // ReceivablesMode: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // contract: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // OtherParty: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // projectClass: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // formTransactionCon: [
        //   { required: true, message: '' }
        // ]
      }
    }
  },
  props: { // 
    map: {
      // type:String
    }
  },
  watch: {
    form: {// 
      handler (val, oldVal) {
        // console.log(val.invoicebo)
        if(val.invoicebo == ''){ //eslint-disable-line
          this.boxvalue = false
        } else {
          this.boxvalue = true
        }
        if(val.Receivableslist == ''){ //eslint-disable-line
          this.boxvalue1 = true
        } else {
          this.boxvalue1 = false
        }
      },
      deep: true // true 
    },
    formModify: {// 
      handler (val, oldVal) {
        // console.log(val.invoicebo)
        if(val.invoicebo == ''){ //eslint-disable-line
          this.boxvalue = false
        } else {
          this.boxvalue = true
        }
        if(val.Receivableslist == ''){ //eslint-disable-line
          this.boxvalue1 = true
        } else {
          this.boxvalue1 = false
        }
      },
      deep: true // true 
    }
  },
  mounted () {
    this.getdata()
    this.getFormData('SupplierList')
    this.getFormDataClass('expenditureClass')
    this.getFormDataPojname()
  },
  methods: {
    submitFormModify (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updatpostData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    updatpostData () { // 
      if ( Number(this.formModify.SupplierName) < 999) { //eslint-disable-line
      } else {
        this.formModify.SupplierName = this.formbf.SupplierName
      }
      if ( Number(this.formModify.projectClass) < 999) { //eslint-disable-line
      } else {
        this.formModify.projectClass = this.formbf.projectClass
      }
      this.jsondata.updatpostData('expenditure', this.formModify).then(response => {
        if (response.data === 'OK') {
          this.dialogAddVisible = false
          this.getdata()
        }
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    postData () { // 
      this.jsondata.postData('expenditure', this.form).then(response => {
        if (response.data === 'OK') {
          this.dialogFormVisible = false
          this.getdata()
          this.resetForm('form')
        }
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    getdata () {
      this.hetongjiner = 0 // 
      this.yingshou = 0 //
      this.zongshouru = 0 //
      this.weishou = 0 //
      this.weikaifapiao = 0
      this.biujifapiao = 0
      this.jsondata.getDataClass('expenditure', this.$route.params.id, 'projectId').then(response => {
        this.tableData = response.data
        this.jsondata.getData('expenditureData').then(responselist => {
          this.formTransactionList = responselist.data
          for (let i = 0; i < this.tableData.length; i++) {
            if(this.tableData[i].Receivableslist == ''){ //eslint-disable-line

            } else {
              this.tableData[i].Receivables = 0
              this.tableData[i].invoice = 0
              this.tableData[i].weikaifapiao = 0
              this.tableData[i].Receivablesend = 0
              for (let is = 0; is < this.formTransactionList.length; is++) {
                if (this.tableData[i].id == this.formTransactionList[is].projectId) { //eslint-disable-line
                  if(this.formTransactionList[is].invoicebo == ''){ //eslint-disable-line
                    this.biujifapiao += Number(this.formTransactionList[is].Receivables)
                  } else {
                    this.tableData[i].invoice += Number(this.formTransactionList[is].invoice)
                  }
                  this.tableData[i].Receivables += Number(this.formTransactionList[is].Receivables)
                }
              }
            }
            this.tableData[i].weikaifapiao = this.jsondata.currency(Number(this.tableData[i].Receivables) - Number(this.tableData[i].invoice), '', 2)
            this.tableData[i].invoice = this.jsondata.currency(this.tableData[i].invoice, '', 2)
            if(this.tableData[i].invoicebo == ''){ //eslint-disable-line
              this.biujifapiao += Number(this.tableData[i].Receivables)
              this.tableData[i].invoice = this.tableData[i].invoicebo
              this.tableData[i].weikaifapiao = this.tableData[i].invoicebo
            }
            this.tableData[i].Receivablesend = this.jsondata.currency(this.tableData[i].number - this.tableData[i].Receivables, '', 2)
            this.tableData[i].number = this.jsondata.currency(this.tableData[i].number, '', 2)
            this.tableData[i].Receivables = this.jsondata.currency(this.tableData[i].Receivables, '', 2)
          }
        })
          .catch(error => {
            console.log(error)
          })
        this.jsondata.getData('SupplierList').then(response => { // 
          this.tableData = this.jsondata.fordata(response.data, this.tableData, 'SupplierName', 'SupplierName')
        })
          .catch(error => {
            console.log(error)
          })
        this.jsondata.getData('expenditureclass').then(response => { // 
          this.tableData = this.jsondata.fordata(response.data, this.tableData, 'expenditureClass', 'projectClass')
        })
          .catch(error => {
            console.log(error)
          })
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    deletepost () { // 
      this.jsondata.deletepost('expenditure', this.formModify.id).then(response => {
        if (response.data === 'OK') {
          this.dialogAddVisible = false
          this.getdata()
        }
      })
        .catch(error => {
          console.log(error)
        })
    },
    handleClick (row) {
      this.$router.push('/expenditure/' + row.id)
    },
    handle (row, event, column) { // 
      // console.log(row)
      this.dialogAddVisible = true
      this.jsondata.getDataId('expenditure', row.id).then(response => {
        this.formModify = response.data[0]
        this.formModify.SupplierName = row.SupplierName
        this.formModify.projectClass = row.projectClass
      })
        .catch(error => {
          console.log(error)
        })
      this.jsondata.getDataId('expenditure', row.id).then(response => {
        this.formbf = response.data[0]
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    getFormData (formName) { // 
      this.jsondata.getData(formName).then(response => {
        this.optionsOtherParty = response.data
      })
        .catch(error => {
          console.log(error)
        })
    },
    getFormDataClass (formName) { // 
      this.jsondata.getData(formName).then(response => {
        this.optionsprojectClass = response.data
      })
        .catch(error => {
          console.log(error)
        })
    },
    getFormDataPojname () {
      this.jsondata.getDataId('projectList', this.$route.params.id).then(response => {
        this.projectName = response.data[0].projectName
      })
        .catch(error => {
          console.log(error)
        })
    },
    goToHome () {
      this.$router.push('/ProjectDetails/' + this.$route.params.id)
    }
  }
}
</script>



