 <template>
  <el-main style="text-align:left; line-height: 1.8em;">
    <h3>
      {{this.$route.params.id}}
      <el-button type="primary" style="float: right;" @click="dialogFormVisible = true"></el-button>
    </h3>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="form" :rules="rules"  label-width="80px" class="demo-ruleForm">
        <el-form-item label="" prop="SupplierName">
          <el-input v-model="form.SupplierName"></el-input>
          <p id='infoint' style="display:none;color:#f00"></p>
        </el-form-item>
        <el-form-item label="" prop="SupplierClass">
            <el-select v-model="form.SupplierClass" placeholder="" style="width:46%" >
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="SupplierCon">
          <el-input type="textarea" v-model="form.SupplierCon" ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')"></el-button>
          <el-button @click="resetForm('form')"></el-button>
          <el-button @click="dialogFormVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog :title="formModify.ReceivablesName" :visible.sync="dialogAddVisible" >
      <el-form ref="formModify" :model="formModify" :rules="rules"  label-width="80px" class="demo-ruleForm">
        <el-form-item label="" prop="SupplierName">
          <el-input v-model="formModify.SupplierName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="SupplierCon">
          <el-input type="textarea" v-model="formModify.SupplierCon" ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitFormModify('formModify')"></el-button>
          <el-button @click="dialogAddVisible = false"></el-button>
          <el-button style="float:right" @click="deletepost"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-table @row-click="handle" :data="tableData" border style="width: 100%">
      <el-table-column prop="SupplierName" label="" sortable ></el-table-column>
      <!-- <el-table-column prop="SupplierClass" label="" sortable></el-table-column> -->
      <!-- <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column> -->
      <el-table-column prop="SupplierCon" label="" sortable></el-table-column>
    </el-table>
  </el-main>
</template>
<script>
// import axios from 'axios'

export default {
  data () {
    return {
      public: [],
      Customerlist: [],
      tableData: [],
      dialogAddVisible: false,
      dialogFormVisible: false,
      formModify: {}, // 
      TransactionList: {},
      formTransactionCon: '',
      formTransactionList: [], // 
      form: {
        SupplierName: '',
        SupplierClass: '',
        SupplierCon: ''
      },
      rules: {
        SupplierName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        SupplierClass: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  props: { // 
    map: {
      // type:String
    }
  },
  watch: {
  },
  mounted () {
    this.getdata()
  },
  methods: {
    submitFormModify (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updatpostData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    updatpostData () { // 
      this.jsondata.updatpostData('SupplierList', this.formModify).then(response => {
        if (response.data === 'OK') {
          this.dialogAddVisible = false
          this.getdata()
        }
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    postData () { // 
      this.jsondata.getData('SupplierList').then(response => {
        for (let i = 0; i < response.data.length; i++) {
          if (this.form.SupplierName === response.data[i].SupplierName) {
            document.querySelector('#infoint').style.display = 'block'
            return false
          } else {
            document.querySelector('#infoint').style.display = 'none'
          }
        }
        this.jsondata.postData('SupplierList', this.form).then(response => {
          if (response.data === 'OK') {
          // console.log(response.data, this.form)
            this.dialogFormVisible = false
            this.getdata()
            this.resetForm('form')
          }
        })
          .catch(error => {
            console.log(error)
          })
      })
        .catch(error => {
          console.log(error)
        })
    },
    getdata () {
      this.jsondata.getData('SupplierList').then(response => {
        this.tableData = response.data
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    deletepost () { // 
      this.jsondata.deletepost('SupplierList', this.formModify.id).then(response => {
        if (response.data === 'OK') {
          this.dialogAddVisible = false
          this.getdata()
        }
      })
        .catch(error => {
          console.log(error)
        })
    },
    handle (row, event, column) { // 
      this.dialogAddVisible = true
      this.formModify = row
      // this.jsondata.getDataId('SupplierList', row.id).then(response => {
      //   this.formModify = response.data[0]
      //   console.log(this.formModify)
      // })
      //   .catch(error => {
      //     console.log(error)
      //   })
      // return false
    }
  }
}
</script>



